.stars {
  display: flex;
  flex-direction: row;
}

.stars__star {
  margin-right: 4px;
  width: 16px;
  height: 16px;
  background: url(./../../images/star.svg) center no-repeat;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars--1 :first-child,
.stars--2 :nth-child(-n+2),
.stars--3 :nth-child(-n+3),
.stars--4 :nth-child(-n+4),
.stars--5 :nth-child(n) {
  background: url(./../../images/star-active.svg) center no-repeat;
}
